export default {
  name: "About",
  transition: "page",
  head () {
    return {
      title: 'Bill Funk - About Me',
    }
  },
  data () {
    return {
      slices: []
    }
  },
  async asyncData({ $prismic, error }) {
    try {
      const about = (await $prismic.api.getSingle('about')).data

      return {
        about
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
};
